ition:relative;display:inline-block;width:60px;height:26px}.switch input{opacity:0;width:0;height:0}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background:#aeb3cb;border-radius:26px;transition:all .4s ease}.slider:before{position:absolute;content:"";height:20px;width:20px;left:4px;bottom:3px;background-color:var(--bg-accent);border-radius:50%;-webkit-transform:translateX(32px);transform:translateX(32px);transition:all .4s ease}input:checked+.slider{background:linear-gradient(to right, #378fe6, #3eda82)}input:checked+.slider:before{background-color:var(--bg-accent);transition:all .4s ease}input:focus+.slider{box-shadow:0 0 1px #2196F3}input:checked+.slider:before{-webkit-transform:translateX(0px);transform:translateX(0px)}

